import React, { Component } from "react";
import CharacterCard from "./components/CharacterCard";
import Wrapper from "./components/Wrapper";
import characters from "./characters.json";
import "./App.css";
import Navbar from "./components/Navbar";

class App extends Component {
  state={
    characters
  };


handleRest = id => {
  this.setState({
    currentScore: 0,
    topScore: this.state.topScore,
    clicked: []
  });
  this.handleShuffle();
}

handleShuffle = id => {
  this.setState({
    
  })
}

  handleRemoval = id => {
    const characters = this.state.characters.filter(character => character.id !== id);
    this.setState({ characters });
  };

  render() {
    return (
      <div>
      <Navbar/>
      <Wrapper>
        <h1 className="title">Sailor Moon Clicky Game!</h1>
        <h4>Cick on any image to earn a point, but don't click on the same picture more than once. Click all 12 to win!!</h4>
        {this.state.characters.map(charObj => {
          return <CharacterCard handleRemoval={this.handleRemoval} id={charObj.id} key={charObj.id} image={charObj.image}/>
        })}
      </Wrapper>
      </div>
    );
  }
}
export default App;